import { SectionSubtitle } from "@mesulive/ui/src";
import {
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from "@mui/material";

export interface RadioGroupStoryProps {
  items?: string[];
  label?: string;
}

export const RadioGroupStory = ({
  items = ["1", "2", "3"],
  label = "",
}: RadioGroupStoryProps) => (
  <FormControl>
    <SectionSubtitle>{label}</SectionSubtitle>
    <RadioGroup>
      {items.map((item) => (
        <FormControlLabel
          value={item}
          control={<Radio />}
          label={item}
          key={item}
        />
      ))}
    </RadioGroup>
  </FormControl>
);
